
import common from '@/libs/common.mixin'
import modal from '@/libs/modal.mixin'
import Popup from 'vux/src/components/popup/index.vue'


export default{
mixins: [ modal,common ],
components: {
Popup
},
props: {
tableName: {
type: String,
default: ''
},
tableId: {
type: String,
default: ''
}
},
data () {
return{
cmt_id: 0,
review_name: '',
info: {
content: ''
}
}
},
watch: {
'modal$.show': function (val) {
if(val&&this.modal$.action=='add'){
this.info.content = ''
}
}
},
methods: {
close () {
this.modal$.show = false
this.$emit('on-close')
},
ok () {
let info = this.info
if(/^\s*$/.test(info.content)){
this.toast('请输入回复内容~')
return
}

let params = {
sid: this.sid$,
content: info.content,
app_name: this.tableName,
app_content_id: this.tableId
}

if(this.cmt_id){
params.parent_cmt_id = this.cmt_id
}

this.$rest('comments').post(params)
.success(data=>{
this.toast('回复成功！','success')
this.$emit('on-success')
this.close()
}).error(response=>{
this.toast(response.body.message||'回复失败~','warn')
})


setTimeout(()=>{
this.close()
},500)
}
},
computed: {
placeholder () {
let name = this.review_name,
result = ''

if(this.cmt_id>0){
result = `回复  ${name}：`
}else{
result = '请输入评论'
}
return result
}
}
}



