
import common from '@/libs/common.mixin'
import modal from '@/libs/modal.mixin'
import comment from './comment.mixin'
import Popup from 'vux/src/components/popup/index.vue'

import CommentAdd from './add.vue'

export default{
mixins: [ common, modal, comment ],
components: {
Popup,
CommentAdd
},
props: {
tableName: {
type: String,
default: ''
},
tableId: {
type: String,
default: 0
},
},
data () {
return {
comment: {}
}
},
methods: {
handleSuccess () {
this.$emit('on-success')
},
close () {
this.modal$.show = false
this.$emit('on-close')
},
}
}



