
import Vue from 'vue'
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

import FileItem from 'c%/FileItem.vue'
import Comment from 'c%/Comment.vue'

export default{
mixins: [ common,globals ],
components: {
Group,
Flexbox,
XButton,
Comment,
FileItem,
FlexboxItem
},
data () {
return {
info: {},
id: this.$route.params.id,
data: [],
list: [],
ready: false,
previewShow: false
}
},
mounted () {
this.init_data()
},
computed: {
sheduleDate () {
let result = ''
if(this.info.int_day){
result = moment(this.$filter('int_date')(this.info.int_day)).format('YYYY年MM月DD日')
}
return result
},
lesson () {
let lessons = this.$store.state.gvars.lessons,
lid = this.info.lid,
result = {}
if(lessons){
result = lessons.find(l=>l.lid==lid)
}
return result
},
},
methods: {
init_data() {
this.$rest('course_prepares/'+this.id).get()
.success(data=>{
this.info = data
this.ready = true
})
}
}
}



