
import util from '@/libs/util'
import comment from './comment/comment.mixin'
import common from '@/libs/common.mixin'
import CommentAdd from './comment/add.vue'
import CommentDetail from './comment/detail.vue'
import Group from 'vux/src/components/group/index.vue'


export default{
mixins: [ common, comment ],
components: {
Group,
CommentAdd,
CommentDetail
},
props: {
tableName: {
type: String,
default: ''
},
tableId: {
type: String,
default: 0
},
title: {
type: String,
default: '评论列表'
}
},
data () {
return {
comment_num: 0,
data: [],
pagesize: 10
}
},
mounted () {

},
methods: {
detail (item) {
this.$refs.commentDetail.set('comment',item,false).show()
},
loadFinished (response) {
this.comment_num = response.body.data.comment_num

}
},
computed: {
data_uri () {

let uri = util.sprintf('comments/tree_comments?with=my_click&app_name=%s&app_content_id=%s&merge_second=1',this.tableName,this.tableId)
return uri
}
}
}




