import moment from 'moment'
import util from '@/libs/util'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'

export default{	
	mixins: [ common ],
	methods: {
		thumbUp (item) {
			let uri = util.sprintf('comments/%s/up',item.cmt_id)
			
			if(!this.isThumbup(item)){	
				this.$rest(uri).post({sid:this.sid$})
				.success(data=>{
					item.up_num += 1
					this.$set(item,'$thumbup',true)
				})
			}else{
				console.log('已经点赞过')
			}
		},
		getCommentUser (item,field){		
			let obj = item.sid>0?'student':'employee',
				result = ''
			if(field == 'create_time'){
				moment.locale('zh-cn')
				result = moment(item[field]).fromNow()
			}else if(field=='name'){
				field = item.sid>0?'student_name':'ename'
				result = item[obj][field]
			}else{
				result = item[obj][field]
			}
			
			return result
		},
		getParentCommentName (child,parent) {
			let parent_cmt_id = child.parent_cmt_id,
				name = ''
			if(parent_cmt_id > 0&&parent){
				let parent_obj = parent.child_comments.find(c=>c.cmt_id==parent_cmt_id)
				if(parent_obj&&(parent_obj.sid>0||parent_obj.eid>0)){
					name = parent_obj.sid>0?parent_obj.student.student_name:parent_obj.employee.ename 
				}
			}
			
			return name
		},
		isThumbup (item) {
			return !_.isEmpty(item.comment_click)||item.$thumbup
		},
		review (item) {
			item = item||{}
			let cmt_id = item.cmt_id||0,
				name = ''
			
			if(item.sid||item.eid){
				if(item.sid>0){
					name = item.student.student_name
				}else{
					name = item.employee.ename
				}
			}
						
			this.$refs.commentAdd
			.set('cmt_id',cmt_id)
			.set('review_name',name)
			.show()
		}
	}
	
}



